
.header {
  @extend %body-content;
  height: 700px;
  background-image: url(../images/header-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: $padding-t-b $padding-r-l;
  padding-bottom: $padding-t-b - 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: $main-header-color;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;

  &__top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &__phone {
    a {
      color: $main-header-color;

      &:last-child {
        visibility: hidden;
      }
    }
  }

  &__logo {
    transform: translate(-50%, -50%);
  }

  &__action {
    height: 24px;
    border-bottom: 1px solid  $main-header-color;
  }

  &__link {
    text-transform: uppercase;
    color: $main-header-color;
  }

  &__line {
    display: flex;
    width: 35px;
    height: 2px;
    background: $main-header-color;
    border-radius: 2px;
    margin-top: $padding-r-l * 2;
  }

  &__title {
    display: flex;
    max-width: 328px;
    max-height: 124px;
    font-size: 60px;
    line-height: 60px;
    letter-spacing: -1.5px;
    margin-top: -$padding-t-b;
  }
}

@media screen and (max-width: 1100px) {

  .header {
    height: 568px;

    &__action {
      visibility: hidden;
    }

    &__phone {

      & a:first-child {
        display: none;
      }

      & a:last-child {
        visibility: visible;
      }
    }
  }
}

@media screen and (max-width: 700px) {

  .header {
    &__line {
      margin-top: $padding-r-l * 2.5;
    }
    &__title {
      max-width: 253px;
      font-size: 46px;
      line-height: 40px;
      letter-spacing: -1px;
      padding-right: 0;
    }
  }
}

@media screen and (max-width: 500px) {

  .header {
    padding: 42px 20px 80px;
  }
}
