
#menuToggle {

  input {
    display: none;
  }

  span {
    display: none;
  }
}

.nav {
  text-transform: uppercase;

  &__list {
    display: flex;
    width: 100%;
    justify-content: space-between;
    list-style: none;
    color: $main-header-color;
  }

  &__item {
    padding: 8px 24px;
    border-radius: 8px;

    &:last-child {
      display: none;
    }
  }

  &__item_active {
    background-color: rgba(161, 123, 123, 0.1);
  }

  &__link {
    text-decoration: none;
    color: $main-header-color;

    &:hover {
      color: #9fadc2;
    }
  }
}

@media screen and (max-width: 1100px) {

  .nav {
    position: absolute;
    right: 24px;
    top: 40px;

    &__list {
      flex-direction: column;
    }

    &__item:last-child {
      display: inline-block;
      font-size: 12px;
      letter-spacing: 2px;
      padding: 14px 0;
      margin: 0 24px;
      border-radius: 0;
      border-bottom: 1px solid  $main-header-color;
    }
  }
}

@media screen and (max-width: 700px) {
  .nav {
    right: 100px;
    top: 56px;
  }

  #menu {
    position: absolute;
    width: 250px;
    margin-top: -100px;
    padding: 20px;
    padding-top: 120px;
    right: -100px;
    background: #ededed27;
    transform-origin: 0% 0%;
    transform: translate(100%, 0);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  }

  #menuToggle {

    span {
      display: block;
      width: 18px;
      height: 2px;
      margin-bottom: 3px;
      background: $main-header-color;
      border-radius: 2px;
      z-index: 1;
      transform-origin: 2px 0;
      transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1) opacity 0.55s;

      &:first-child {
        transform-origin: 0% 0%;
      }
      &:nth-last-child(2) {
        transform-origin: 0% 100%;
      }
    }

    input {

      &:checked ~ span {
        opacity: 1;
        transform: rotate(45deg);

        &:nth-last-child(3) {
          opacity: 0;
          transform: rotate(0deg) scale(0.2, 0.2);
        }

        &:nth-last-child(2) {
          opacity: 1;
          transform: rotate(-45deg);
        }
      }

      &:checked ~ ul {
        transform: none;
        opacity: 1;
      }
    }
  }
}

@media screen and (max-width: 500px) {

  .nav {
    right: 20px;
    top: 42px;
  }
}
